<template>
  <el-dialog 
    :visible="dialogNewFIVisible"
    :before-close="TOGGLE_NEW_OTHERFI_DIALOG"
    :width="'35%'"
    title="Nghĩa vụ tín dụng mới">
    <el-form 
      :model="form" 
      :rules="rules" 
      ref="newOtherFIForm" 
      label-width="140px" 
      size="mini" 
      label-position="left">
      <el-form-item label="Tổ chức tín dụng" prop="credit_institution">
        <el-select 
          v-model="form.credit_institution"
          :remote-method="remoteMethod"
          :loading="loading"
          filterable
          remote 
          reserve-keyword>
          <el-option
            v-for="(f, i) in options" 
            :key="i"
            :label="f.label" 
            :value="f.value" />
        </el-select>
      </el-form-item>

      <el-form-item label="Loại khoản vay" prop="loan_type">
        <el-select 
          v-model="form.loan_type"
          filterable>
          <el-option
            v-for="(p, i) in dataset.typeOfLoan" 
            :key="i"
            :label="p.label" 
            :value="p._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Dư nợ" prop="outstanding">
        <el-input v-model.number="form.outstanding">
          <template slot="append">đồng</template>
        </el-input>
      </el-form-item>

      <el-form-item label="Mức/hạn mức" prop="limit">
        <el-input v-model.number="form.limit">
          <template slot="append">đồng</template>
        </el-input>
      </el-form-item>

      <el-form-item label="Thời hạn còn lại" prop="remain_period">
        <el-input v-model.number="form.remain_period">
          <template slot="append">tháng</template>
        </el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button size="small" type="primary" @click="submit('newOtherFIForm', false)">Save</el-button>
      <el-button size="small" type="primary" @click="submit('newOtherFIForm', true)">Save & Add Another</el-button>
      <el-button size="small" @click="TOGGLE_NEW_OTHERFI_DIALOG">Cancel</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'
import { FI, typeOfLoan } from '@/api/otherFI'

export default {
  computed: {
    ...mapGetters('iDecision', ['dialogNewFIVisible'])
  },

  data: () => ({
    dataset: { FI },

    form: {
      credit_institution: null,
      loan_type: null,
      outstanding: null,
      limit: null,
      remain_period: null
    },

    rules: {
      credit_institution : [{ required: true, message: 'Vui lòng chọn', trigger: 'change' }],
      loan_type          : [{ required: true, message: 'Vui lòng chọn', trigger: 'change' }],
      outstanding        : [{ required: true, message: 'Vui lòng nhập', trigger: 'blur' }],
      limit              : [{ required: true, message: 'Vui lòng nhập', trigger: 'blur' }],
      remain_period      : [{ required: true, message: 'Vui lòng nhập', trigger: 'blur' }]
    },

    loading: false,
    options: []
  }),

  created() {
    this.$services.iDecisionReference.get({ params: { dataset: 'obligation' }})
      .then(res => this.dataset.typeOfLoan = res.data.loan_type)
  },

  methods: {
    ...mapMutations('iDecision', ['TOGGLE_NEW_OTHERFI_DIALOG']),
    ...mapActions('iDecision', ['addOtherFI']),

    submit(formName, keep) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.addOtherFI(JSON.parse(JSON.stringify(this.form)))
          if (!keep) this.TOGGLE_NEW_OTHERFI_DIALOG()
        }
        return false
      })
    },

    remoteMethod(query) {
      if (query !== '') {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.options = this.dataset.FI.filter(item => {
            return item.label.toLowerCase()
              .indexOf(query.toLowerCase()) > -1
          })
        }, 50)
      } else {
        this.options = []
      }
    }
  }
}
</script>
<style lang="css" scoped>
>>>.el-select {
  width: 100%;
}
>>>.el-dialog__body {
  padding: 20px 30px;
}
</style>



