<template>
  <div>
    <el-table
      :data="_case.capacity.obligation"
      border 
      ref="FITable"
      size="mini"
      style="width: 100%">
      <el-table-column type="index" width="35" align="center" />
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :sortable="c.sortable">
        <template slot-scope="scope">
          <span v-if="['limit', 'outstanding'].includes(c.prop)">
            {{ scope.row[c.prop] | numberWithDots }}
          </span>
          <span v-else>
            {{ scope.row[c.prop] }}
          </span>
        </template>
      </el-table-column>
      <el-table-column width="40" fixed="right">
        <template slot-scope="scope">
          <el-button 
            type="text" 
            class="danger--text" 
            icon="icon icon-bin2"
            @click.native.prevent="remove(scope.$index)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button 
      type="primary" 
      class="mt-2" 
      size="mini" 
      plain
      @click="TOGGLE_NEW_OTHERFI_DIALOG">
      <i class="icon icon-plus el-icon--left" /> Thêm khoản vay
    </el-button>
    <new-loan-dialog />
  </div>
</template>
<script>
import { mapMutations, mapGetters, mapActions } from 'vuex'
import NewLoanDialog from './NewLoanDialog'

export default {
  name: 'iDecision-obligation',

  components: {
    NewLoanDialog
  },

  data: () => ({
    columnDefs: [
      { label: 'Tổ chức tín dụng', prop: 'credit_institution', sortable: true },
      { label: 'Loại khoản vay', prop: 'loan_type', sortable: true },
      { label: 'Dư nợ', prop: 'outstanding', sortable: true },
      { label: 'Hạn mức/Hạn mức chưa giải ngân', prop: 'limit', sortable: true },
      { label: 'Kì hạn còn lại (tháng)', prop: 'remain_period', sortable: true }
    ]
  }),

  computed: {
    ...mapGetters('iDecision', ['_case'])
  },

  methods: {
    ...mapMutations('iDecision', ['TOGGLE_NEW_OTHERFI_DIALOG']),
    ...mapActions('iDecision', ['removeOtherFI']),

    remove(index) {
      this.$confirm('This will permanently delete the file. Continue?', 'Warning', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.removeOtherFI(index)
      })
    }
  }
}
</script>



