<template>
  <div class="row">
    <!-- <h3 class="mt-0 mb-1 row subtitle-2">Các chỉ số rủi ro</h3> -->
    <el-row :gutter="10" type="flex" class="mb-2">
      <el-col :span="2">
        <span class="pl-4 font-weight-medium"> PD </span>
      </el-col>
      <el-col :span="1" class="text-md-center">
        <el-tag size="small" class="font-weight-medium">{{ _case.indicator.credit_rating }}</el-tag>
      </el-col>
      <el-col :span="3" class="text-md-center">
        <el-tag :type="_case.indicator.PD || 'danger'" size="small" class="font-weight-medium">
          {{ _case.indicator.PD ? _case.indicator.PD + '%' : 'No data' }}
        </el-tag>
      </el-col>
      <!-- <el-col :span="3" :offset="15" class="text-md-center">
        <el-tag effect="dark" size="small" class="font-weight-bold">Chưa phù hợp</el-tag>
      </el-col> -->
    </el-row>

    <el-row :gutter="10" type="flex" class="mb-2">
      <el-col :span="3">
        <span class="pl-4 font-weight-medium"> LGD </span>
      </el-col>
      <el-col :span="3" class="text-md-center">
        <el-tag :type="_case.indicator.LGD || 'danger'" size="small" class="font-weight-medium">
          {{ _case.indicator.LGD ? _case.indicator.LGD + '%' : 'No data' }}
        </el-tag>
      </el-col>
      <!-- <el-col :span="3" :offset="15" class="text-md-center">
        <el-tag effect="dark" size="small" class="font-weight-bold">Chưa phù hợp</el-tag>
      </el-col> -->
    </el-row>

    <el-row :gutter="10" type="flex" class="mb-2">
      <el-col :span="3">
        <span class="pl-4 font-weight-medium"> EL </span>
      </el-col>
      <el-col :span="3" class="text-md-center">
        <el-tag :type="_case.indicator.EL || 'danger'" size="small" class="font-weight-medium">
          {{ _case.indicator.EL ? _case.indicator.EL + '%' : 'No data' }}
        </el-tag>
      </el-col>
      <!-- <el-col :span="3" :offset="15" class="text-md-center">
        <el-tag effect="dark" size="small" class="font-weight-bold">Chưa phù hợp</el-tag>
      </el-col> -->
    </el-row>
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields'

export default {
  name: 'iDecision-risk-indicator',

  data: () => ({
    customColors: [
      {color: '#f56c6c', percentage: 100},
      {color: '#e6a23c', percentage: 80},
      {color: '#6f7ad3', percentage: 60},
      {color: '#1989fa', percentage: 40},
      {color: '#5cb87a', percentage: 20}
    ],
    options: [
      { value: 'Phù hợp', label: 'Phù hợp' }, 
      { value: 'Không phù hợp', label: 'Không phù hợp' }, 
      { value: 'Không đánh giá', label: 'Không đánh giá' }
    ]
  }),

  computed: {
    ...mapFields('iDecision', ['_case'])
  }
}
</script>
<style scoped>
  >>> .el-progress {
    line-height: 1.6 !important;
  }
</style>



