<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="OCDSalaryForm"
    label-width="220px" 
    size="mini">
    <el-form-item label="Đối tượng chứng minh" prop="subject">
      <el-select v-model="form.subject">
        <el-option 
          v-for="(o, i) in subjects" 
          :key="i"
          :label="o.label" 
          :value="o.value" />
      </el-select>
    </el-form-item>

    <el-form-item label="TN chứng minh được" prop="proved_income">
      <el-input 
        v-model.number="form.proved_income"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="TN không chứng minh được" prop="unproved_income">
      <el-input 
        v-model.number="form.unproved_income"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Chứng từ chứng minh" prop="is_document">
      <el-radio-group v-model="form.is_document" size="mini">
        <el-radio-button :label="true">Có</el-radio-button>
        <el-radio-button :label="false">Không</el-radio-button>
      </el-radio-group>
    </el-form-item>

    <el-form-item label="Tên cơ quan" prop="work_place_name">
      <el-select
        v-model="form.work_place_name"
        filterable
        allow-create>
        <el-option
          v-for="(item, i) in dataset.work_place_name"
          :key="i"
          :label="item.label"
          :value="item._id">
        </el-option>
      </el-select>
    </el-form-item>

    <el-form-item v-if="!isExisted" label="Nhóm cơ quan" prop="work_place_group">
      <el-select v-model="form.work_place_group">
        <el-option 
          v-for="(o, i) in dataset.work_place_group" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item v-if="isPrivateCompany" label="Ngành nghề kinh doanh" prop="business_field">
      <el-select v-model="form.business_field">
        <el-option 
          v-for="(o, i) in dataset.business_field" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Địa bàn cơ quan" prop="work_place_area">
      <el-select v-model="form.work_place_area">
        <el-option 
          v-for="(o, i) in dataset.work_place_area" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Chuyên môn nghề nghiệp" prop="job_speciality">
      <el-select v-model="form.job_speciality">
        <el-option 
          v-for="(o, i) in dataset.job_speciality" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item v-if="otherIncome" label="Số giờ làm thêm/tháng" prop="overtime_hours_per_month">
      <el-select v-model="form.overtime_hours_per_month">
        <el-option 
          v-for="(o, i) in dataset.overtime_hours_per_month" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Vị trí công tác" prop="job_position">
      <el-select v-model="form.job_position">
        <el-option 
          v-for="(o, i) in dataset.job_position" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Trình độ học vấn" prop="learning_level">
      <el-select v-model="form.learning_level">
        <el-option 
          v-for="(o, i) in dataset.learning_level" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Loại HĐLĐ" prop="labor_contract_type">
      <el-select v-model="form.labor_contract_type">
        <el-option 
          v-for="(o, i) in dataset.labor_contract_type" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item prop="past_seniority">
      <label slot="label">TNCT trong quá khứ
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            Thâm niên công tác không tính thời gian công tác tại đơn vị hiện tại
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-select v-model="form.past_seniority">
        <el-option 
          v-for="(o, i) in dataset.past_seniority" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item prop="current_seniority">
      <label slot="label">TNCT hiện tại
        <el-tooltip effect="dark" placement="top">
          <div slot="content">Thời gian công tác tại đơn vị hiện tại</div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-select v-model="form.current_seniority">
        <el-option 
          v-for="(o, i) in dataset.current_seniority" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Hình thức nhận thu nhập" prop="income_receiving_type">
      <el-select v-model="form.income_receiving_type">
        <el-option 
          v-for="(o, i) in dataset.income_receiving_type" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>
  </el-form>
</template>

<script>
import { subjects } from '@/api/otherFI'

export default {
  name: 'OCD-salary',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    },

    type: {
      type: String,
      default: () => 'Unknown'
    },

    description: {
      type: String,
      default: () => 'Unknown'
    }
  },

  mounted() {
    this.initReferenceData()
    if (this.formData) this.form = this.formData
  },

  data() {
    const isNumber = (rule, value, callback) => {
      if (!value) return callback(new Error('Vui lòng nhập'))
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Vui lòng nhập số'))
        }
        callback()
      }, 50)
    }
    const type = this.type
    const description = this.description
    return {
      labelPosition: 'left',
      // Dataset
      subjects,
      dataset: {},
      formName: 'OCDSalaryForm',
      form: {
        type,
        description,
        subject                  : null,
        proved_income            : null,
        unproved_income          : null,
        is_document              : null,
        work_place_name          : null,
        work_place_group         : null,
        business_field           : null,
        work_place_area          : null,
        overtime_hours_per_month : null,
        job_speciality           : null,
        job_position             : null,
        learning_level           : null,
        past_seniority           : null,
        current_seniority        : null,
        labor_contract_type      : null,
        income_receiving_type    : null
      },

      rules: {
        subject                  : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        proved_income            : [{ validator: isNumber, required: true, trigger: 'blur' }],
        unproved_income          : [{ validator: isNumber, required: true, trigger: 'blur' }],
        is_document              : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        work_place_name          : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        work_place_group         : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        business_field           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        work_place_area          : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        overtime_hours_per_month : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        job_speciality           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        job_position             : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        learning_level           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        past_seniority           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        current_seniority        : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        labor_contract_type      : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        income_receiving_type    : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }]
      },
      // Work place type
      loading: false,
      options: []
    }
  },

  // watch: (
  //   (vm) => (vm.x, vm.y, vm.z, Date.now()),
  //     function () {
  //       // Executes if `x`, `y`, or `z` have changed.
  //     }
  // )

  computed: {
    isExisted() {
      const wpList = this.dataset.work_place_name
      const wpName = this.form.work_place_name

      if (wpList && wpName !== null) {
        return wpList.findIndex(x => x._id === wpName) !== -1
      }
      return true
    },

    otherIncome() {
      const wpList      = this.dataset.work_place_name
      const wpGroupList = this.dataset.work_place_group
      const jsList      = this.dataset.job_speciality

      const wpId      = this.form.work_place_name
      const wpGroupId = this.form.work_place_group
      const jsID      = this.form.job_speciality

      if (wpList && wpId !== null) {
        const wp  = wpList.find(x => x._id === wpId)
        const wpG = wpGroupList.find(x => x._id === wpGroupId)
        const js  = jsList.find(x => x._id === jsID)

        if (wp) {
          if (wp.group_oc === '1') {
            this.dataset.job_position = this.dataset.job_position_goverment
            return true
          } else {
            this.dataset.job_position = this.dataset.job_position_company
          }
        }
        if (wpG) { 
          if (wpG.group_oc === '1') {
            this.dataset.job_position = this.dataset.job_position_goverment
            return true
          } else {
            this.dataset.job_position = this.dataset.job_position_company
          }
        }
        if (js) if (js.other_income) return true
      }
    },

    isPrivateCompany() {
      const wpList      = this.dataset.work_place_name
      const wpGroupList = this.dataset.work_place_group
      const wpId        = this.form.work_place_name
      const wpGroupId   = this.form.work_place_group

      if (wpList && wpId !== null) {
        const wp  = wpList.find(x => x._id === wpId)
        const wpG = wpGroupList.find(x => x._id === wpGroupId)

        if (wp) if (wp.is_private_company) return true
        if (wpG) if (wpG.is_private_company) return true
      }
    }
  },

  watch: {
    'form.work_place_name'(val) {
      if (!this.readOnly) {
        this.form.work_place_group = null
        this.form.overtime_hours_per_month = null
        this.form.business_field = null
      }
    },

    'form.work_place_group'() {
      if (!this.readOnly) {
        this.form.overtime_hours_per_month = null
        this.form.business_field = null
      }
    },

    'form.job_speciality'() {
      if (!this.readOnly) this.form.overtime_hours_per_month = null
    }
  },

  methods: {
    initReferenceData() {
      this.$store.dispatch(
        'iDecision/retrieveReferenceData', 
        { dataset: 'OCD_salary' },
        { root: true }
      )
        .then(dataset => this.dataset = dataset)
        .then(data => this.options = this.dataset.work_place_name.filter(p => p._id === this.form.work_place_name))
    },

    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch(
            'iDecision/addIncome', 
            { payload: this.form, path: 'salary', keep: keep }, 
            { root: true } 
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>



