<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="RSVAutoForm"
    label-width="200px" 
    size="mini">
    <el-form-item label="Giá trị định giá" prop="valuation_value">
      <el-input 
        v-model.number="form.valuation_value"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>
    
    <el-form-item label="NV đang đảm bảo" prop="guarantee_value">
      <el-input 
        v-model.number="form.guarantee_value"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Thông tin xe" prop="car_detail">
      <el-cascader v-if="!readOnly" v-model="form.car_detail" :options="cascader" :props="props" clearable />
      <el-input v-else :value="form.car_detail.join(' / ')" :title="form.car_detail.join(' / ')" />
    </el-form-item>

    <el-form-item prop="no_years_different">
      <label slot="label">
        Số năm chênh lệch
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            Độ chênh giữa năm bắt đầu sử dụng và năm sản xuất
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-radio-group v-model="form.no_years_different">
        <el-radio-button label="0" value="0" />
        <el-radio-button label="1" value="1" />
        <el-radio-button label="2" value="2" />
      </el-radio-group>
    </el-form-item>

    <el-form-item label="Số chỗ" prop="no_seats">
      <el-input-number v-model.number="form.no_seats" controls-position="right" :min="0" />
    </el-form-item>

    <el-form-item prop="load">
      <label slot="label">
        Tải trọng
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            Đơn vị tấn
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-input-number v-model.number="form.load" controls-position="right" :min="0" />
    </el-form-item>

    <el-form-item label="Giá trị HĐMB xe" prop="price">
      <el-input v-model.number="form.price">
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Năm sản xuất" prop="manufacturing_year">
      <el-input-number v-model.number="form.manufacturing_year" controls-position="right" :min="1900" :max="new Date().getFullYear()" />
    </el-form-item>

    <el-form-item label="Số Km đã đi" prop="traveled_distance">
      <el-input-number v-model.number="form.traveled_distance" controls-position="right" :min="0" />
    </el-form-item>

    <el-form-item label="Số khung" prop="VIN">
      <el-input v-model="form.VIN" />
    </el-form-item>

    <el-form-item label="Số máy" prop="engine_number">
      <el-input v-model="form.engine_number" />
    </el-form-item>

    <el-form-item label="Biển kiểm soát" prop="plate_number">
      <el-input v-model="form.plate_number" />
    </el-form-item>
  </el-form>
</template>

<script>
import { cascader } from '@/api/data_sample'
export default {
  name: 'RSV-auto',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    }
  },

  data() {
    let car_detail = []
    const self = this
    return {
      labelPosition: 'left',
      formName: 'RSVAutoForm',
      // Dataset
      cascader: cascader,
      form: {
        type: 'auto',
        description: 'Ôtô',
        valuation_value: null,
        guarantee_value: null,
        car_detail: [],
        no_seats: null,
        no_years_different: null,
        load: null,
        price: null,
        manufacturing_year: null,
        traveled_distance: null,
        VIN: null,
        engine_number: null,
        plate_number: null
      },
      title: {
        2: 'Nơi sản xuất',
        3: 'Hãng xe',
        4: 'Dòng xe',
        5: 'Dòng xe chi tiết',
        6: 'Kiểu xe',
        7: 'Dung tích',
        8: 'Hộp số'
      },
      props: {
        lazy: true,
        async lazyLoad(node, resolve) {
          const { level } = node

          car_detail = car_detail.slice(0, level)
          car_detail[level - 1] = node.value

          if (!['Xe mới', 'Xe cũ'].includes(node.value)) {
            setTimeout(() => {
              self.$store.dispatch(
                'iDecision/retrieveReferenceData', 
                { dataset: 'auto', car_detail: car_detail.slice(0, level).join(',') },
                { root: true }
              ).then(res => {
                const node = res.map(item => ({
                  value: item,
                  label: item,
                  leaf: level >= 8
                }))
                node.unshift({
                  value: self.title[level],
                  label: self.title[level],
                  disabled: true
                })
                resolve([...new Set(node)])
              })
            }, 300)
          } else {
            resolve([
              { value: 'Loại xe', label: 'Loại xe', disabled: true },
              { value: 'Xe tải', label: 'Xe tải', leaf: false },
              { value: 'Xe du lịch', label: 'Xe du lịch', leaf: false },
              { value: 'Xe khách', label: 'Xe khách', leaf: false }
            ])
          }
        }
      },
      rules: {
        valuation_value    : [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
        guarantee_value    : [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
        car_detail         : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        no_seats           : [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
        no_years_different : [{ required: true, message: 'Vui lòng nhập ', trigger: 'change' }],
        price              : [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
        manufacturing_year : [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
        traveled_distance  : [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
        VIN                : [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }]
      }
    }
  },

  mounted() {
    if (this.formData) this.form = this.formData
  },

  methods: {
    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch(
            'iDecision/addCollateral', 
            { payload: this.form, path: 'auto', keep: keep }, 
            { root: true } 
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>



