<template>
  <div>
    <el-tabs v-model="assessmentActivePanels" :tab-position="'left'">
      <!-- <el-tab-pane label="Quy định">
        <regulation />
      </el-tab-pane> -->
      <el-tab-pane label="Khả năng chi trả" name="capacity">
        <payment-capacity />
      </el-tab-pane>
      <el-tab-pane label="Tài sản bảo đảm" name="collateral">
        <collateral class="ml-3 row" />
      </el-tab-pane>
      <el-tab-pane label="Chỉ số rủi ro" name="indicator">
        <risk-indicator />
      </el-tab-pane>
    </el-tabs>
    
    
    
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields'
import Regulation from './Regulation/index'
import RiskIndicator from './RiskIndicator/index'
import PaymentCapacity from './PaymentCapacity/index'
import Collateral from './Collateral/index'

export default {
  name: 'iDecision-assessment',

  components: {
    Regulation,
    RiskIndicator,
    PaymentCapacity,
    Collateral
  },

  computed: {
    ...mapFields('iDecision', ['assessmentActivePanels'])
  },
}
</script>
<style lang="css" scoped>
.el-row, .row {
  margin-right: 25px !important;
  &:last-child {
    margin-bottom: 0;
  }
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
</style>


