<template>
  <div class="row">
   <!--  <el-row :gutter="10" type="flex">
      <el-col :span="3" :offset="18">
        <span class="font-weight-medium mr-2"> DTI </span>
        <el-tag size="medium" class="font-weight-medium">123123</el-tag>
      </el-col>
      <el-col :span="3" class="text-md-center">
        <el-tag effect="dark" size="small" class="font-weight-bold">Chưa phù hợp</el-tag>
      </el-col>
    </el-row> -->

    <span class="pl-3 font-weight-bold">Nguồn trả nợ</span>
    <income class="ml-3 my-2" />
    <span class="pl-3 font-weight-bold">Nghĩa vụ tín dụng của Khách hàng</span>
    <obligation class="ml-3 my-2" />
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields'
import Income from './Income/index'
import Obligation from './Obligation/index'

export default {
  name: 'iDecision-payment-capacity',

  components: {
    Income,
    Obligation
  },

  data: () => ({
    value: null,
    options: [
      { value: 'Phù hợp', label: 'Phù hợp' }, 
      { value: 'Không phù hợp', label: 'Không phù hợp' }, 
      { value: 'Không đánh giá', label: 'Không đánh giá' }
    ]
  }),

  computed: {
    ...mapFields('iDecision', ['_case'])
  }
}
</script>



