<template>
  <el-row :gutter="40">
    <h3 class="mt-0 mb-1">Thông tin khách hàng</h3>
    <el-form 
      :model="_case.proposal" 
      :rules="rules" 
      :label-position="'left'" 
      label-width="180px" 
      ref="ProposalCustomerInformationForm" 
      size="mini">
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Tên khách hàng">
          <span>{{ _case.proposal.customer_name }}</span>
        </el-form-item>
        <el-form-item label="Số định danh">
          <span>{{ _case.proposal.identity_number }}</span>
        </el-form-item>
        <el-form-item label="ID khách hàng">
          <span>{{ _case.proposal.customer_id }}</span>
        </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Tuổi">
          <span>{{ _case.proposal.age }}</span>
        </el-form-item>
        <el-form-item label="Tình trạng hôn nhân">
          <span>{{ _case.proposal.marital_status }}</span>
        </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Thông tin CIC" prop="is_CIC">
          <el-select v-model="_case.proposal.is_CIC">
            <el-option label="Có" :value="1" />
            <el-option label="Không" :value="0" />
          </el-select>
        </el-form-item>
        <el-form-item label="Thông tin giao dịch" prop="relation_with_TCB">
          <el-select v-model="_case.proposal.relation_with_TCB">
            <el-option 
              v-for="(o, i) in dataset.relation_with_TCB" 
              :key="i"
              :label="o.label" 
              :value="o._id" />
          </el-select>
        </el-form-item>
      </el-col>
    </el-form>
  </el-row>
</template>

<script>
import { mapFields } from 'vuex-map-fields'

export default {
  name: 'iDecision-proposal-customer-information',

  props: {
    dataset: {
      type: Object,
      default: {}
    }
  },

  data: () => ({
    rules: {
      is_CIC: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      relation_with_TCB: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }]
    }
  }),

  computed: {
    ...mapFields('iDecision', ['_case'])
  }
}
</script>
<style scoped>
>>>.el-select, 
>>>.el-input-number--mini {
  width: 100%;
}
</style>



