<template>
  <el-row :gutter="40">
    <h3 class="my-1">Nhu cầu của khách hàng</h3>
    <el-form 
      :model="_case.proposal" 
      :rules="rules"
      :label-position="'left'" 
      label-width="180px" 
      ref="ProposalNeedForm" 
      size="mini">
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Mục đích vay">
          <span>{{ _case.proposal.loan_purpose }}</span>
        </el-form-item>
        <el-form-item label="Sản phẩm">
          <span>{{ _case.proposal.product }}</span>
        </el-form-item>
      </el-col>

      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Chương trình" prop="program">
          <el-select v-model="_case.proposal.program" size="mini">
            <el-option 
              v-for="(o, i) in dataset.program" 
              :key="i"
              :label="o.label" 
              :value="o._id" />
            </el-select>
        </el-form-item>
        <el-form-item label="Chi tiết" prop="program_detail">
          <el-select v-model="_case.proposal.program_detail" size="mini">
            <el-option 
              v-for="(o, i) in dataset.program_detail" 
              :key="i"
              :label="o.label" 
              :value="o._id" />
            </el-select>
          </el-select>
        </el-form-item>
        <el-form-item label="Giá trị phương án" prop="plan_value">
          <el-input v-model.number="_case.proposal.plan_value">
            <template slot="append">đồng</template>
          </el-input>
        </el-form-item>
      </el-col>

      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Số tiền vay" prop="loan_amount">
          <el-input v-model.number="_case.proposal.loan_amount">
            <template slot="append">đồng</template>
          </el-input>
        </el-form-item>
        <el-form-item label="Thời hạn vay" prop="tenor">
          <el-input v-model.number="_case.proposal.tenor">
            <template slot="append">tháng</template>
          </el-input>
        </el-form-item>
        <el-form-item label="Phương thức trả nợ" prop="payment_method">
          <el-select v-model="_case.proposal.payment_method" size="mini">
            <el-option 
              v-for="(o, i) in dataset.payment_method" 
              :key="i"
              :label="o.label" 
              :value="o._id" />
            </el-select>
          </el-select>
        </el-form-item>
      </el-col>
    </el-form>
  </el-row>
</template>

<script>
import { mapFields } from 'vuex-map-fields'

export default {
  name: 'iDecision-proposal-need',

  props: {
    dataset: {
      type: Object,
      default: {}
    }
  },

  data() {
    const isNumber = (rule, value, callback) => {
      if (!value) return callback(new Error('Vui lòng nhập'))
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Vui lòng nhập số'))
        }
        callback()
      }, 50)
    }
    return {
      rules: {
        program        : [{ required: true, message: 'Vui lòng chọn', trigger: 'change' }],
        program_detail : [{ required: true, message: 'Vui lòng chọn', trigger: 'change' }],
        plan_value     : [{ validator: isNumber, required: true, trigger: 'blur' }],
        loan_amount    : [{ validator: isNumber, required: true, trigger: 'blur' }],
        tenor          : [{ validator: isNumber, required: true, trigger: 'blur' }],
        payment_method : [{ required: true, message: 'Vui lòng chọn', trigger: 'change' }]
      }
    }
  },

  computed: {
    ...mapFields('iDecision', ['_case'])
  },

  watch: {
    '_case.proposal.program'(program) {
      if (this.dataset) {
        this.dataset.program_detail = this.dataset.program.find(p => p._id === program).children
        this._case.proposal.program_detail = null
      }
    }
  },

  created() {
    if (this._case.proposal.program)
      this.dataset.program_detail = this.dataset.program.find(p => p._id === this._case.proposal.program).children
  }
}
</script>
<style scoped>
>>>.el-select, 
>>>.el-input-number--mini {
  width: 100%;
}
</style>


