<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="ISVApartmentForm"
    label-width="200px" 
    size="mini">
    <el-form-item label="Giá trị định giá" prop="valuation_value">
      <el-input 
        v-model.number="form.valuation_value"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>
    
    <el-form-item label="NV đang đảm bảo" prop="guarantee_value">
      <el-input 
        v-model.number="form.guarantee_value"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Tên dự án" prop="project_name">
      <el-select v-model="form.project_name" filterable allow-create>
        <el-option 
          v-for="(p, i) in dataset.project" 
          :key="i"
          :label="p.project_name" 
          :value="p._id" />
      </el-select>
    </el-form-item>

    <el-form-item v-if="!isExistedProject" label="Phân khúc" prop="segment">
      <el-radio-group v-model="form.segment" size="mini">
        <el-radio-button 
          v-for="(p, i) in dataset.segment" 
          :key="i"
          :label="p._id">
          {{ p.segment }}
        </el-radio-button>
      </el-radio-group>
    </el-form-item>

    <el-form-item v-if="!isExistedProject" label="Năm xây dựng" prop="construction_year">
      <el-input-number 
        v-model.number="form.construction_year" 
        :min="0" 
        controls-position="right" />
    </el-form-item>

    <el-form-item prop="area">
      <label slot="label">Diện tích căn hộ
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            Đơn vị m2
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-input-number 
        v-model.number="form.area" 
        :min="0" 
        controls-position="right" />
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'ISV-apartment',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    }
  },

  mounted() {
    this.initReferenceData()
    if (this.formData) this.form = this.formData
  },

  data: () => ({
    labelPosition: 'left',
    formName: 'ISVApartmentForm',
    // Dataset
    dataset: {},

    form: {
      type: 'apartment',
      description: 'BĐS chung cư',
      valuation_value: null,
      guarantee_value: null,
      project_name: null,
      segment: null,
      construction_year: null,
      area: null
    },

    rules: {
      valuation_value: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      guarantee_value: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      project_name: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      segment: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      construction_year: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      area: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }]
    },
    isExistedProject: true
  }),

  watch: {
    'form.project_name'(project_id) {
      if (this.dataset !== {}) {
        const projects = this.dataset.project
        this.isExistedProject = projects.findIndex(p => p.id === project_id) !== -1
      } else {
        this.isExistedProject = true
      }
      this.segment = null
      this.construction_year = null
    }
  },

  methods: {
    initReferenceData() {
      this.$store.dispatch(
        'iDecision/retrieveReferenceData', 
        { dataset: 'apartment' },
        { root: true }
      ).then(dataset => this.dataset = dataset)
    },

    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch(
            'iDecision/addCollateral', 
            { payload: this.form, path: 'apartment', keep: keep }, 
            { root: true } 
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>



