<template>
  <div>
    <customer-information v-if="dataset" :dataset="dataset" ref="information" />
    <!-- <other-target /> -->
    <need v-if="dataset" :dataset="dataset" ref="need" />
  </div>
</template>

<script>
import CustomerInformation from '@/components/iDecision/Case/Proposal/CustomerInformation'
import OtherTarget from '@/components/iDecision/Case/Proposal/OtherTarget'
import Need from '@/components/iDecision/Case/Proposal/Need'

export default {
  name: 'iDecision-proposal',

  components: {
    CustomerInformation,
    OtherTarget,
    Need
  },

  data: () => ({
    dataset: null
  }),

  created() {
    this.initReferenceData()
  },

  methods: {
    initReferenceData() {
      this.$store.dispatch(
        'iDecision/retrieveReferenceData', 
        { dataset: 'general' },
        { root: true }
      ).then(dataset => this.dataset = dataset)
    }
  }
}
</script>
<style lang="css" scoped>
.el-row {
  margin-left: 20px !important;
  margin-right: 20px !important;
  &:last-child {
    margin-bottom: 0;
  }
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
</style>



