<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="CompanyIncomeForm"
    label-width="180px" 
    size="mini">
    <el-form-item label="Đối tượng chứng minh" prop="subject">
      <el-select v-model="form.subject">
        <el-option 
          v-for="(o, i) in subjects" 
          :key="i"
          :label="o.label" 
          :value="o.value" />
      </el-select>
    </el-form-item>

    <el-form-item label="Loại BCTC" prop="provide">
      <el-radio-group v-model="form.financial_report">
        <el-radio-button label="internal">BCTC nội bộ</el-radio-button>
        <el-radio-button label="external">BCTC kiểm toán/thuế</el-radio-button>
      </el-radio-group>
    </el-form-item>

    <el-form-item label="Lợi nhuận" prop="profit">
      <el-input 
        v-model.number="form.profit"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>
  </el-form>
</template>

<script>
import { subjects } from '@/api/otherFI'

export default {
  name: 'income-company',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    },

    type: {
      type: String,
      default: () => 'Unknown'
    },

    description: {
      type: String,
      default: () => 'Unknown'
    }
  },

  mounted() {
    if (this.formData) this.form = this.formData
  },

  data() {
    const isNumber = (rule, value, callback) => {
      if (!value) return callback(new Error('Vui lòng nhập'))
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Vui lòng nhập số'))
        }
        callback()
      }, 50)
    }
    const type = this.type
    const description = this.description
    return {
      labelPosition: 'left',
      // Dataset
      subjects,
      dataset: {},
      formName: 'CompanyIncomeForm',
      form: {
        type,
        description,
        subject           : null,
        financial_report : null,
        profit           : null
      },

      rules: {
        subject           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        financial_report : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        profit           : [{ validator: isNumber, required: true, trigger: 'blur' }]
      }
    }
  },

  methods: {
    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch(
            'iDecision/addIncome', 
            { payload: this.form, path: 'company', keep: keep }, 
            { root: true } 
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>



