<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="OCDHouseholdForm"
    label-width="190px" 
    size="mini">
    <el-form-item label="Loại hình kinh doanh" prop="householdBusinessType">
      <el-radio-group v-model="form.householdBusinessType" size="mini">
        <el-radio-button label="s">Thương mại dịch vụ</el-radio-button>
        <el-radio-button label="m">Sản xuất chế biến</el-radio-button>
      </el-radio-group>
    </el-form-item>

    <el-form-item label="Đối tượng chứng minh" prop="subject">
      <el-select v-model="form.subject">
        <el-option 
          v-for="(o, i) in subjects" 
          :key="i"
          :label="o.label" 
          :value="o.value" />
      </el-select>
    </el-form-item>

    <el-form-item label="Ngành nghề" prop="industry">
      <el-cascader
        v-model="form.industry"
        :props="{ value: '_id', label: 'label' }"
        :options="dataset.industry" 
        filterable />
    </el-form-item>
    
    <el-form-item label="Doanh thu" prop="revenue">
      <el-input 
        v-model.number="form.revenue"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Lợi nhuận" prop="profit">
      <el-input 
        v-model.number="form.profit"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>
    
    <div v-if="form.householdBusinessType === 's'">
      <el-form-item prop="s.service_past_bussiness_experience">
        <label slot="label">Kinh nghiệm trước đây
          <el-tooltip effect="dark" placement="top">
            <div slot="content">
              Kinh nghiệm của KH trong lĩnh vực kinh doanh không tính thời gian kinh doanh hiện tại 
            </div>
            <i class="el-icon-warning" />
          </el-tooltip>
        </label>
        <el-select v-model="form.s.service_past_bussiness_experience">
          <el-option 
            v-for="(o, i) in dataset.service_past_bussiness_experience" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>
      
      <el-form-item prop="s.service_current_bussiness_experience">
        <label slot="label">Kinh nghiệm hiện tại
          <el-tooltip effect="dark" placement="top">
            <div slot="content">
              Thời gian đã hoạt động của HKD hiện tại
            </div>
            <i class="el-icon-warning" />
          </el-tooltip>
        </label>
        <el-select v-model="form.s.service_current_bussiness_experience">
          <el-option 
            v-for="(o, i) in dataset.service_current_bussiness_experience" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>
      
      <el-form-item label="Địa điểm kinh doanh" prop="s.service_bussiness_location">
        <el-select v-model="form.s.service_bussiness_location">
          <el-option 
            v-for="(o, i) in dataset.service_bussiness_location" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item prop="s.service_bussiness_location_ownership">
        <label slot="label">Hình thức sở hữu
          <el-tooltip effect="dark" placement="top">
            <div slot="content">
              Hình thức sở hữu địa điểm kinh doanh
            </div>
            <i class="el-icon-warning" />
          </el-tooltip>
        </label>
        <el-select v-model="form.s.service_bussiness_location_ownership">
          <el-option 
            v-for="(o, i) in dataset.service_bussiness_location_ownership" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Cơ sở vật chất" prop="s.service_bussiness_infrastructure">
        <el-select v-model="form.s.service_bussiness_infrastructure">
          <el-option 
            v-for="(o, i) in dataset.service_bussiness_infrastructure" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>
      
      <el-form-item label="Hình thức phân phối" prop="s.service_distribution_type">
        <el-select v-model="form.s.service_distribution_type">
          <el-option 
            v-for="(o, i) in dataset.service_distribution_type" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Số lượng nhân viên" prop="s.service_number_of_employees">
        <el-select v-model="form.s.service_number_of_employees">
          <el-option 
            v-for="(o, i) in dataset.service_number_of_employees" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item prop="s.service_relationship_duration_with_traditional_partner">
        <label slot="label">Thời gian quan hệ
          <el-tooltip effect="dark" placement="top">
            <div slot="content">
              Thời gian quan hệ với các bạn hàng truyền thống
            </div>
            <i class="el-icon-warning" />
          </el-tooltip>
        </label>
        <el-select v-model="form.s.service_relationship_duration_with_traditional_partner">
          <el-option 
            v-for="(o, i) in dataset.service_relationship_duration_with_traditional_partner" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Uy tín với nhà cung cấp" prop="s.service_reputation_with_supplier">
        <el-select v-model="form.s.service_reputation_with_supplier">
          <el-option 
            v-for="(o, i) in dataset.service_reputation_with_supplier" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Hình thức nhận thu nhập" prop="s.service_proved_document">
        <el-select v-model="form.s.service_proved_document">
          <el-option 
            v-for="(o, i) in dataset.proved_document" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>
    </div>
    
    <div v-else-if="form.householdBusinessType === 'm'">
      <el-form-item label="Tuổi chủ hộ kinh doanh" prop="m.manufacturing_age_of_bussiness_owner">
        <el-select v-model="form.m.manufacturing_age_of_bussiness_owner">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_age_of_bussiness_owner" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item prop="m.manufacturing_past_bussiness_experience">
        <label slot="label">Kinh nghiệm trước đây
          <el-tooltip effect="dark" placement="top">
            <div slot="content">
              Kinh nghiệm của KH trong lĩnh vực kinh doanh không tính thời gian kinh doanh hiện tại 
            </div>
            <i class="el-icon-warning" />
          </el-tooltip>
        </label>
        <el-select v-model="form.m.manufacturing_past_bussiness_experience">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_past_bussiness_experience" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>
      
      <el-form-item prop="m.manufacturing_current_bussiness_experience">
        <label slot="label">Kinh nghiệm hiện tại
          <el-tooltip effect="dark" placement="top">
            <div slot="content">
              Thời gian đã hoạt động của HKD hiện tại
            </div>
            <i class="el-icon-warning" />
          </el-tooltip>
        </label>
        <el-select v-model="form.m.manufacturing_current_bussiness_experience">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_current_bussiness_experience" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item prop="m.manufacturing_is_family_continuation">
        <label slot="label">Kế cận?
          <el-tooltip effect="dark" placement="top">
            <div slot="content">
              Hộ kinh doanh có sự kế cận của các thế hệ trong gia đình?
            </div>
            <i class="el-icon-warning" />
          </el-tooltip>
        </label>
        <el-select v-model="form.m.manufacturing_is_family_continuation">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_is_family_continuation" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Địa điểm kinh doanh" prop="m.manufacturing_bussiness_area">
        <el-select v-model="form.m.manufacturing_bussiness_area">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_bussiness_area" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item prop="m.manufacturing_bussiness_location_ownership">
        <label slot="label">Hình thức sở hữu
          <el-tooltip effect="dark" placement="top">
            <div slot="content">
              Hình thức sở hữu địa điểm kinh doanh
            </div>
            <i class="el-icon-warning" />
          </el-tooltip>
        </label>
        <el-select v-model="form.m.manufacturing_bussiness_location_ownership">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_bussiness_location_ownership" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Cơ sở vật chất" prop="m.manufacturing_bussiness_infrastructure">
        <el-select v-model="form.m.manufacturing_bussiness_infrastructure">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_bussiness_infrastructure" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>
      
      <el-form-item label="Hình thức phân phối" prop="m.manufacturing_distribution_type">
        <el-select v-model="form.m.manufacturing_distribution_type">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_distribution_type" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Số lượng nhân viên" prop="m.manufacturing_number_of_employees">
        <el-select v-model="form.m.manufacturing_number_of_employees">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_number_of_employees" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item prop="m.manufacturing_relationship_duration_with_traditional_partner">
        <label slot="label">Thời gian quan hệ
          <el-tooltip effect="dark" placement="top">
            <div slot="content">
              Thời gian quan hệ với các bạn hàng truyền thống
            </div>
            <i class="el-icon-warning" />
          </el-tooltip>
        </label>
        <el-select v-model="form.m.manufacturing_relationship_duration_with_traditional_partner">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_relationship_duration_with_traditional_partner" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Uy tín với nhà cung cấp" prop="m.manufacturing_reputation_with_supplier">
        <el-select v-model="form.m.manufacturing_reputation_with_supplier">
          <el-option 
            v-for="(o, i) in dataset.manufacturing_reputation_with_supplier" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>

      <el-form-item label="Hình thức nhận thu nhập" prop="m.manufacturing_proved_document">
        <el-select v-model="form.m.manufacturing_proved_document">
          <el-option 
            v-for="(o, i) in dataset.proved_document" 
            :key="i"
            :label="o.label" 
            :value="o._id" />
        </el-select>
      </el-form-item>
    </div>
  </el-form>
</template>

<script>
import { subjects } from '@/api/otherFI'

export default {
  name: 'OCD-household',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    },

    type: {
      type: String,
      default: () => 'Unknown'
    },

    description: {
      type: String,
      default: () => 'Unknown'
    }
  },

  mounted() {
    this.initReferenceData()
    if (this.formData) {
      this.form[this.formData.householdBusinessType] = this.formData
      this.form.subject = this.formData.subject
      this.form.profit = this.formData.profit
      this.form.industry = this.formData.industry
      this.form.revenue = this.formData.revenue
      this.form.householdBusinessType = this.formData.householdBusinessType
    }
  },

  data() {
    const isNumber = (rule, value, callback) => {
      if (!value) return callback(new Error('Vui lòng nhập'))
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Vui lòng nhập số'))
        }
        callback()
      }, 50)
    }
    const type = this.type
    const description = this.description
    return {
      subjects,
      labelPosition: 'left',
      // Dataset
      dataset: {},
      formName: 'OCDHouseholdForm',
      form: {
        subject               : null,
        industry              : null,
        profit                : null,
        revenue               : null,
        householdBusinessType : 's',
        s: {
          type,
          description, 
          service_proved_document                                : null,
          service_bussiness_infrastructure                       : null,
          service_bussiness_location                             : null,
          service_bussiness_location_ownership                   : null,
          service_current_bussiness_experience                   : null,
          service_distribution_type                              : null,
          service_number_of_employees                            : null,
          service_past_bussiness_experience                      : null,
          service_relationship_duration_with_traditional_partner : null,
          service_reputation_with_supplier                       : null
        },
        m: {
          type,
          description, 
          manufacturing_age_of_bussiness_owner                         : null,
          manufacturing_bussiness_area                                 : null,
          manufacturing_bussiness_infrastructure                       : null,
          manufacturing_bussiness_location_ownership                   : null,
          manufacturing_current_bussiness_experience                   : null,
          manufacturing_distribution_type                              : null,
          manufacturing_is_family_continuation                         : null,
          manufacturing_number_of_employees                            : null,
          manufacturing_past_bussiness_experience                      : null,
          manufacturing_relationship_duration_with_traditional_partner : null,
          manufacturing_reputation_with_supplier                       : null,
          manufacturing_proved_document                                : null
        }
      },
      rules: {
        subject               : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        industry              : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        profit                : [{ validator: isNumber, required: true, trigger: 'blur' }],
        revenue               : [{ validator: isNumber, required: true, trigger: 'blur' }],
        householdBusinessType : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        m: {
          manufacturing_age_of_bussiness_owner                         : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_bussiness_area                                 : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_bussiness_infrastructure                       : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_bussiness_location_ownership                   : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_current_bussiness_experience                   : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_distribution_type                              : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_is_family_continuation                         : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_number_of_employees                            : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_past_bussiness_experience                      : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_relationship_duration_with_traditional_partner : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_reputation_with_supplier                       : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          manufacturing_proved_document                                : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }]
        },
        s: {
          service_bussiness_infrastructure                       : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          service_bussiness_location                             : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          service_bussiness_location_ownership                   : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          service_current_bussiness_experience                   : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          service_distribution_type                              : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          service_number_of_employees                            : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          service_past_bussiness_experience                      : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          service_relationship_duration_with_traditional_partner : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          service_reputation_with_supplier                       : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
          service_proved_document                                : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }]
        }
      }
    }
  },

  methods: {
    initReferenceData() {
      this.$store.dispatch(
        'iDecision/retrieveReferenceData', 
        { dataset: 'OCD_household' },
        { root: true }
      ).then(dataset => this.dataset = dataset)
    },

    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          const type = this.form.householdBusinessType
          const path = type === 's' ? 'service' : 'manufacturing'

          this.form[type].subject = this.form.subject
          this.form[type].householdBusinessType = this.form.householdBusinessType
          this.form[type].profit = this.form.profit
          this.form[type].industry = this.form.industry
          this.form[type].revenue = this.form.revenue
          
          this.$store.dispatch(
            'iDecision/addIncome', 
            { payload: this.form[type], path: `household/${path}`, keep: keep }, 
            { root: true } 
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>



