<template>
  <div>
    <el-table
      :data="_case.capacity.incomes"
      border
      size="mini"
      ref="incomeTable"
      style="width: auto">
      <el-table-column type="index" width="35" align="center" fixed="left" />

      <el-table-column
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :fixed="c.fixed"
        :sortable="c.sortable">
        <template slot-scope="scope" >
          <span v-if="c.prop === 'subject'" :class="c.class">
            {{ subjects.find(t => t.value === scope.row[c.prop]).label }}
          </span>
          <span v-else :class="c.class">
            {{ scope.row[c.prop] | numberWithDots }}
          </span>
        </template>
      </el-table-column>

      <el-table-column
        fixed="right"
        width="75">
        <template slot-scope="scope">
          <el-button 
            type="text" 
            class="info--text"
            icon="icon icon-search"
            @click.native.prevent="viewDetail(scope.row)">
          </el-button>
          <el-button
            type="text"
            class="danger--text"
            icon="icon icon-bin2"
            @click.native.prevent="removeIncome(scope.$index)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dropdown 
      class="mt-2" 
      trigger="click" 
      placement="bottom-start"
      @command="selectIncome">

      <el-button type="primary" size="mini" plain>
        <i class="icon icon-plus el-icon--left" /> Thêm nguồn thu
      </el-button>

      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item 
          v-for="i in income_opts"
          :key="i.component"
          :icon="`el-icon-${i.icon}`"
          :command="i">
          {{ i.label }}
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <NewIncomeDialog :component="component" :readOnly="readOnly" :formData="formData" />
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'
import { subjects } from '@/api/otherFI'
import NewIncomeDialog from './NewIncomeDialog'

export default {
  name: 'iDecision-income',

  components: {
    NewIncomeDialog
  },

  computed: {
    ...mapGetters('iDecision', ['_case'])
  },

  data: () => ({
    subjects,
    income_opts: [
      { id: 'salary', label: 'Lương', icon: 'money', component: 'OCDSalary' },
      { id: 'household', label: 'Hộ kinh doanh', icon: 's-home', component: 'OCDHouseholdBusiness' },
      { id: 'real_estate', label: 'Khai thác bất động sản', icon: 'office-building', component: 'OCDRealEstate' },
      { id: 'movables', label: 'Khai thác động sản', icon: 'truck', component: 'OCDAuto' },
      { id: 'company', label: 'Doanh nghiệp', icon: 'office-building', component: 'Company' },
      { id: 'expect', label: 'Dự kiến khai thác tài sản', icon: 'office-building', component: 'ExpectedAssetExploitation' },
      { id: 'liquidation', label: 'Thanh lý tài sản', icon: 's-finance', component: 'Liquidation' },
      { id: 'others', label: 'Khác', icon: 's-tools', component: 'OthersIncome' }
    ],

    component: null,
    readOnly: false,
    formData: null,

    columnDefs: [
      { label: 'Loại nguồn thu', prop: 'description', sortable: true, fixed: 'left' },
      { label: 'Chủ thể', prop: 'subject', sortable: true },
      { label: 'Giá trị thẩm định', prop: 'appraised_value', sortable: true, fixed: 'right' }
    ]
  }),

  methods: {
    ...mapMutations('iDecision', ['TOGGLE_NEW_INCOME_DIALOG']),
    ...mapActions('iDecision', ['removeIncome']),

    selectIncome(component) {
      console.log(component)
      this.readOnly = false
      this.formData = null
      this.component = component
      this.TOGGLE_NEW_INCOME_DIALOG()
    },

    viewDetail(income) {
      this.formData = income
      this.readOnly = true
      this.component = this.income_opts.find(i => i.id === income.type)
      this.TOGGLE_NEW_INCOME_DIALOG()
    }
  }
}
</script>



