<template>
  <el-row :gutter="40">
    <el-form 
      :model="_case.opinion" 
      :rules="rules"
      :label-position="'left'" 
      label-width="180px" 
      ref="ApprovalOpinionForm"
      size="mini">
      <el-col :xs="24">
        <el-form-item label="Ý kiến đề xuất" prop="recommendation">
          <el-radio-group v-model="_case.opinion.recommendation" size="mini">
            <el-radio-button label="Đồng ý"></el-radio-button>
            <el-radio-button label="Không đồng ý"></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <!-- <el-form-item label="Mục đích vay">
          <span>{{ _case.opinion.loan_purpose }}</span>
        </el-form-item>
        <el-form-item label="Sản phẩm">
          <span>{{ _case.opinion.product }}</span>
        </el-form-item> -->
      </el-col>

      <!-- <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Chương trình" prop="program">
          <el-select v-model="_case.opinion.program" size="mini">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Chi tiết" prop="program_detail">
          <el-select v-model="_case.opinion.program_detail" size="mini">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Giá trị phương án" prop="plan_value">
          <el-select v-model="_case.opinion.plan_value" size="mini">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>

      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
        <el-form-item label="Số tiền vay" prop="loan_amount">
          <el-input v-model="_case.opinion.loan_amount" size="mini">
            <template slot="append">đồng</template>
          </el-input>
        </el-form-item>
        <el-form-item label="Thời hạn vay" prop="tenor">
          <el-input v-model="_case.opinion.tenor" size="mini">
            <template slot="append">tháng</template>
          </el-input>
        </el-form-item>
        <el-form-item label="Phương thức trả nợ" prop="payment_method">
          <el-select v-model="_case.opinion.payment_method" size="mini">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col> -->

      <el-col :span="16">
        <el-form-item label="Điều kiện ngoại lệ">
          <el-input
            v-model="_case.opinion.exception_condition"
            type="textarea"
            autosize>
          </el-input>
        </el-form-item>
        <el-form-item label="Điều kiện giải ngân">
          <el-input
            v-model="_case.opinion.disbursement_condition"
            type="textarea"
            autosize>
          </el-input>
        </el-form-item>
        <el-form-item label="Điều kiện khác">
          <el-input
            v-model="_case.opinion.other_condition"
            type="textarea"
            autosize>
          </el-input>
        </el-form-item>
      </el-col>

      <el-col :span="8">
        <el-form-item label="Phân luồng phê duyệt" prop="flow">
          <el-dropdown trigger="click" @command="handleCommandFlow">
            <span class="el-dropdown-link">
              <el-tag :type="tags[_case.opinion.approval_flow]" size="medium" class="font-weight-bold">
                {{ flow }}
              </el-tag>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                v-for="(v, i) in flows"
                :key="i"
                :command="v.value">
                {{ v.label }}
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-form-item>
        <el-form-item label="Cấp phê duyệt" prop="rank">
          <el-dropdown trigger="click" @command="handleCommandRank">
            <span class="el-dropdown-link">
              <el-tag size="medium" class="font-weight-bold">
                {{ _case.opinion.approval_level || 'Please select' }}
              </el-tag>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                v-for="(v, i) in ranks"
                :key="i"
                :command="v.value">
                {{ v.label }}
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-form-item>
      </el-col>
    </el-form>
  </el-row>
</template>

<script>
import { mapFields } from 'vuex-map-fields'

export default {
  name: 'iDecision-approval-opinion',

  data() {
    const isNumber = (rule, value, callback) => {
      if (!value) return callback(new Error('Vui lòng nhập'))
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Vui lòng nhập số'))
        }
        callback()
      }, 50)
    }
    return {
      flows: [
        { label: 'Auto Approval', value: 'auto' },
        { label: 'High Level Approval', value: 'high_level' }
      ],
      ranks: [
        { label: 'C3', value: 'C3' },
        { label: 'C2', value: 'C2' },
        { label: 'C1', value: 'C1' },
        { label: 'B3', value: 'B3' },
        { label: 'B2', value: 'B2' },
        { label: 'B1', value: 'B1' },
        { label: 'A3', value: 'A3' },
        { label: 'A2', value: 'A2' },
        { label: 'A1', value: 'A1' },
        { label: 'HĐTD', value: 'HĐTD' }
      ],
      tags: {
        auto: 'success',
        high_level: 'danger'
      },
      rules: {
        // recommendation: [{ required: true, message: 'Vui lòng chọn', trigger: 'change' }],
        // program: [{ required: true, message: 'Vui lòng chọn', trigger: 'change' }],
        // program_detail: [{ required: true, message: 'Vui lòng chọn', trigger: 'change' }],
        // plan_value: [{ validator: isNumber, required: true, trigger: 'blur' }],
        // loan_amount: [{ validator: isNumber, required: true, trigger: 'blur' }],
        // tenor: [{ validator: isNumber, required: true, trigger: 'blur' }],
        // payment_method: [{ required: true, message: 'Vui lòng chọn', trigger: 'change' }],
        // flow: [{ required: false, message: 'Vui lòng chọn', trigger: 'change' }],
        // rank: [{ required: false, message: 'Vui lòng chọn', trigger: 'change' }]
      }
    }
  },

  computed: {
    ...mapFields('iDecision', ['_case']),

    flow() {
      const flow = this.flows.find(f => f.value === this._case.opinion.approval_flow)
      if (flow) return flow.label
      return 'Please select'
    }
  },

  methods: {
    handleCommandFlow(flow) {
      this._case.opinion.approval_flow = flow
    },

    handleCommandRank(rank) {
      this._case.opinion.approval_level = rank
    }
  }
}
</script>
<style lang="css" scoped>
.el-row {
  margin-left: 20px !important;
  margin-right: 20px !important;
  &:last-child {
    margin-bottom: 0;
  }
}
>>>.el-select, 
>>>.el-input-number--mini {
  width: 100%;
}
</style>



