<template>
  <div class="row">
    <el-row 
      v-for="(v, i) in items"
      :key="i"
      :gutter="10" 
      type="flex"
      class="mb-2">
      <el-col :span="8">
        <span :class="[v.main ? 'pl-3 font-weight-bold' : 'pl-4']">
          {{ v.label }}
        </span>
      </el-col>
      <el-col :span="10">
        <el-input v-if="!v.main" placeholder="Lý do chưa phù hợp (nếu có)" size="mini" />
      </el-col>
      <el-col :span="3" class="text-md-center">
        <el-tag v-if="v.main" effect="dark" size="small" class="font-weight-bold">3/{{ items.length }}</el-tag>
      </el-col>
      <el-col :span="3" class="text-md-center">
        <el-dropdown v-if="!v.main" trigger="click" @command="handleCommand">
          <span class="el-dropdown-link">
            <el-tag size="small" class="font-weight-bold">Chưa phù hợp</el-tag>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item
              v-for="(o, i) in options"
              :key="i"
              :command="o">
              {{ o.label }}
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-tag v-else effect="dark" size="small" class="font-weight-bold">Chưa phù hợp</el-tag>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields'

export default {
  name: 'iDecision-regulation-legal',

  data: () => ({
    options: [
      { value: 'fit', label: 'Phù hợp' }, 
      { value: 'inconsonant', label: 'Không phù hợp' }, 
      { value: 'no_evaluation', label: 'Không đánh giá' }
    ],
    items: [
      { label: 'Quy định pháp luật', value: 'legal', main: true },
      { label: 'Năng lực dân sự', value: 'civil_capacity' },
      { label: 'Mục đích vay vốn', value: 'purpose' },
      { label: 'Tổng dư nợ KH tại TCB', value: 'main_ost' },
      { label: 'Tổng dư nợ KH và nhóm KH liên quan tại TCB', value: 'threshold_ost' }
    ]
  }),

  computed: {
    ...mapFields('iDecision', ['_case'])
  },

  created() {
    // this.$watch(vm => [vm.proposal_value, vm.value], val => {
    //   this.LTD = Math.round((this.value / this.proposal_value) * 100 * 100) / 100
    // }, {immediate: true}) // run immediately
  },

  methods: {
    assessment_options(r) {
      return JSON.parse(JSON.stringify(this.options)).map(o => { o.regulation = r; return o })
    },

    handleCommand(assess) {
      const index = this.items.findIndex(r => r.value === assess.regulation)
      console.log(index)
      if (index === -1) {
        this.items[1].assess = '12312'
        console.log(this.items)
      }
    }
  }
}
</script>



