<template>
  <div class="row">
    <el-row 
      v-for="(v, i) in items"
      :key="i"
      :gutter="10" 
      type="flex"
      class="mb-2">
      <el-col :span="8">
        <span :class="[v.main ? 'pl-3 font-weight-bold' : 'pl-4']">
          {{ v.label }}
        </span>
      </el-col>
      <el-col :span="10">
        <el-input v-if="!v.main" placeholder="Lý do chưa phù hợp (nếu có)" size="mini" />
      </el-col>
      <el-col :span="3" class="text-md-center">
        <el-tag v-if="v.main" effect="dark" size="small" class="font-weight-bold">3/{{ items.length }}</el-tag>
      </el-col>
      <el-col :span="3" class="text-md-center">
        <!-- <el-select v-if="!v.main" v-model="value" size="mini">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select> -->
        <el-tag :effect="!v.main || 'dark'" size="small" class="font-weight-bold">Chưa phù hợp</el-tag>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields'

export default {
  name: 'iDecision-regulation-legal',

  data: () => ({
    value: null,
    options: [
      { value: 'Phù hợp', label: 'Phù hợp' }, 
      { value: 'Không phù hợp', label: 'Không phù hợp' }, 
      { value: 'Không đánh giá', label: 'Không đánh giá' }
    ],
    items: [
      { label: 'Quy định sản phẩm theo từng thời kỳ', value: 'legal', main: true },
      { label: 'Năng lực dân sự', value: 'civil_capacity' },
      { label: 'Mục đích vay vốn', value: 'purpose' },
      { label: 'Tổng dư nợ KH tại TCB', value: 'main_ost' },
      { label: 'Tổng dư nợ KH và nhóm KH liên quan tại TCB', value: 'threshold_ost' }
    ]
  }),

  computed: {
    ...mapFields('iDecision', ['_case'])
  }
}
</script>



