<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="OCDRealEstateForm"
    label-width="220px" 
    size="mini">
    <el-form-item label="Đối tượng chứng minh" prop="subject">
      <el-select v-model="form.subject">
        <el-option 
          v-for="(o, i) in subjects" 
          :key="i"
          :label="o.label" 
          :value="o.value" />
      </el-select>
    </el-form-item>

    <el-form-item label="Mức thu nhập" prop="income">
      <el-input 
        v-model.number="form.income"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Địa bàn" prop="real_estate_area">
      <el-select v-model="form.real_estate_area">
        <el-option 
          v-for="(o, i) in dataset.real_estate_area" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>
    
    <el-form-item label="Vị trí BĐS" prop="real_estate_location">
      <el-select v-model="form.real_estate_location">
        <el-option 
          v-for="(o, i) in dataset.real_estate_location" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>
    
    <el-form-item label="Hình thức sở hữu BĐS" prop="real_estate_ownership">
      <el-select v-model="form.real_estate_ownership">
        <el-option 
          v-for="(o, i) in dataset.real_estate_ownership" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Kinh nghiệm cho thuê" prop="real_estate_rental_experience">
      <el-select v-model="form.real_estate_rental_experience">
        <el-option 
          v-for="(o, i) in dataset.real_estate_rental_experience" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item prop="average_fill_rate">
      <label slot="label">
        Tỷ lệ lấp đầy
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            Tỷ lệ lấp đầy trung bình trong 1 năm
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-select v-model="form.average_fill_rate">
        <el-option 
          v-for="(o, i) in dataset.average_fill_rate" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>
    
    <el-form-item label="Thời hạn hợp đồng" prop="real_estate_rental_contract_term">
      <el-select v-model="form.real_estate_rental_contract_term">
        <el-option 
          v-for="(o, i) in dataset.real_estate_rental_contract_term" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Tần suất nhận tiền thuê" prop="real_estate_receiving_frequency">
      <el-select v-model="form.real_estate_receiving_frequency">
        <el-option 
          v-for="(o, i) in dataset.real_estate_receiving_frequency" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Hình thức nhận thu nhập" prop="real_estate_proved_document">
      <el-select v-model="form.real_estate_proved_document">
        <el-option 
          v-for="(o, i) in dataset.real_estate_proved_document" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>
  </el-form>
</template>

<script>
import { subjects } from '@/api/otherFI'

export default {
  name: 'OCD-real-estate',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    },

    type: {
      type: String,
      default: () => 'Unknown'
    },

    description: {
      type: String,
      default: () => 'Unknown'
    }
  },

  mounted() {
    this.initReferenceData()
    if (this.formData) this.form = this.formData
  },

  data() {
    const isNumber = (rule, value, callback) => {
      if (!value) return callback(new Error('Vui lòng nhập'))
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Vui lòng nhập số'))
        }
        callback()
      }, 50)
    }
    const type = this.type
    const description = this.description
    return {
      labelPosition: 'left',
      // Dataset
      subjects,
      dataset: {},
      formName: 'OCDRealEstateForm',
      form: {
        type,
        description,
        subject                          : null,
        income                           : null,
        average_fill_rate                : null,
        real_estate_area                 : null,
        real_estate_location             : null,
        real_estate_ownership            : null,
        real_estate_proved_document      : null,
        real_estate_receiving_frequency  : null,
        real_estate_rental_contract_term : null,
        real_estate_rental_experience    : null
      },
      rules: {
        subject                          : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        income                           : [{ validator: isNumber, required: true, trigger: 'blur' }],
        average_fill_rate                : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        real_estate_area                 : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        real_estate_location             : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        real_estate_ownership            : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        real_estate_proved_document      : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        real_estate_receiving_frequency  : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        real_estate_rental_contract_term : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        real_estate_rental_experience    : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }]
      }
    }
  },

  methods: {
    initReferenceData() {
      this.$store.dispatch(
        'iDecision/retrieveReferenceData', 
        { dataset: 'OCD_real_estate' },
        { root: true }
      ).then(dataset => this.dataset = dataset)
    },

    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch(
            'iDecision/addIncome', 
            { payload: this.form, path: 'leasing_real_estate', keep: keep }, 
            { root: true }
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>



