<template>
  <div>
    <el-collapse v-model="caseActivePanels">
      <el-collapse-item
        v-for="(c, i) in items" 
        :key="i"
        :name="i">
        <template slot="title">
          <span class="primary--text">&nbsp;{{ c.title }}</span>
        </template>
        <component v-if="_case !== {}" :is="c.component" :ref="c.component" />
      </el-collapse-item>
    </el-collapse>
    <el-button-group class="my-2 mb-4 right">
      <el-button :loading="loading" :disabled="!_case.opinion.recommendation" size="small" icon="el-icon-printer" @click="export_">Xuất PDF</el-button>
      <el-button :loading="loading" size="small" icon="el-icon-download" @click="save">Lưu</el-button>
      <el-tooltip class="item" effect="dark" content="Tính toán kết quả" placement="bottom-start">
        <el-button :loading="loading" type="primary" size="small" icon="el-icon-s-tools" @click="submit">Kết quả</el-button>
      </el-tooltip>
    </el-button-group>
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields'
import { mapActions } from 'vuex'
import Proposal from '@/components/iDecision/Case/Proposal/index'
import Assessment from '@/components/iDecision/Case/Assessment/index'
import Opinion from '@/components/iDecision/Case/ApprovalOpinion/index'

export default {
  name: 'idecision-case-detail',

  components: {
    Proposal,
    Assessment,
    Opinion
  },

  data: () => ({
    items: [
      { title: 'Thông tin & nhu cầu của khách hàng', component: 'proposal' },
      { title: 'Đánh giá thông tin', component: 'assessment' },
      { title: 'Đề xuất phê duyệt', component: 'opinion' }
    ],
    loading: false
  }),

  computed: {
    ...mapFields('iDecision', ['_case', 'caseActivePanels', 'assessmentActivePanels'])
  },

  watch: {
    // Fix table bounce in collapse
    // activePanels() {
    //   this.$refs.Income[0].$refs.incomeTable.$ready = this.activePanels === [2, 3, 4]
    //   this.$refs.Collateral[0].$refs.collateralTable.$ready = this.activePanels === [2, 3, 4]
    //   this.$refs.OtherFI[0].$refs.FITable.$ready = this.activePanels === [2, 3, 4]
    // }
  },

  created() {
    const case_id = this.$route.params.case_id
    this.$services.iDecisionCase.get(case_id)
      .then(res => {
        this._case = res.data
        this.$store.dispatch('changeHeading', `BÁO CÁO ĐÁNH GIÁ TÍN DỤNG - ${res.data.proposal.customer_name} - ${res.data.case_id}`)
        if (!this._case.status) this.caseActivePanels.push(2)
      })

  },

  beforeRouteLeave (to, from, next) { 
    this.$confirm('Do you really want to leave? you have unsaved changes!', 'Warning', {
      confirmButtonText: 'Yes',
      cancelButtonText: 'No',
      type: 'warning'
    }).then(_ => next())
  },

  methods: {
    ...mapActions('iDecision', ['retrieveCase']),

    submit() {
      this.save()

      const ProposalCustomerInformationForm = this.$refs.proposal[0].$refs.information.$refs.ProposalCustomerInformationForm
      const ProposalNeedForm = this.$refs.proposal[0].$refs.need.$refs.ProposalNeedForm
      let valid = []

      ProposalCustomerInformationForm.validate((v) => valid.push(v))
      ProposalNeedForm.validate((v) => valid.push(v))

      if (!valid.includes(false)) {
        this.loading = true
        this.$services.iDecisionEL.post(this._case)
          .then(res => {
            this._case.indicator.PD = (res.data.PD * 100).toFixed(2)
            this._case.indicator.LGD = (res.data.LGD * 100).toFixed(2)
            this._case.indicator.EL = (res.data.EL * 100).toFixed(2)
            if (this.caseActivePanels.indexOf(2) === -1) this.caseActivePanels.push(2)
            this.assessmentActivePanels = 'indicator'
            this.$message.success({
              message: 'Thành công!',
              showClose: false
            })
          })
          .catch(err => this.$message.error({
            message: err,
            showClose: false
          }))
          .finally(_ => this.loading = false)
      }
    },

    save() {
      this.loading = true
      this.$services.iDecisionCase.put(this._case.case_id, this._case)
        .finally(_ => this.loading = false)
    },

    export_() {
      this.$axios({
        url: process.env.IDECISION_API + '/export',
        method: 'POST',
        responseType: 'blob', // important
        data: { case_id: this._case.case_id }
      })
        .then(res => {
          const url = window.URL.createObjectURL(new Blob([res.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', 'export.pdf')
          document.body.appendChild(link)
          link.click()
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
</script>
<style lang="css" scoped>
>>>.el-collapse-item__header {
  padding-left: 15px;
  padding-right: 15px;
  font-size: 16px;
  /*background-color: #efefef;*/
} 
</style>



