<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="ISVGroundForm"
    label-width="200px" 
    size="mini">
    <el-form-item label="Giá trị định giá" prop="valuation_value">
      <el-input 
        v-model.number="form.valuation_value"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>
    
    <el-form-item label="NV đang đảm bảo" prop="guarantee_value">
      <el-input 
        v-model.number="form.guarantee_value"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Tên dự án" prop="project_name">
      <el-select v-model="form.project_name" filterable>
        <el-option 
          v-for="(o, i) in dataset.project" 
          :key="i"
          :label="o.label" 
          :value="o.label" />
      </el-select>
    </el-form-item>

    <el-form-item label="Loại đất nền" prop="ground_type">
      <el-select v-model="form.ground_type" filterable>
        <el-option 
          v-for="(o, i) in dataset.ground_type" 
          :key="i"
          :label="o.label" 
          :value="o.label" />
      </el-select>
    </el-form-item>

    <el-form-item prop="land_area">
      <label slot="label">Diện tích đất
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            Đơn vị m2
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-input-number 
        v-model.number="form.land_area" 
        :min="0" 
        controls-position="right" />
    </el-form-item>

    <el-form-item prop="GFA">
      <label slot="label">Tổng diện tích sàn XD
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            Đơn vị m2
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-input-number 
        v-model.number="form.GFA" 
        :min="0" 
        controls-position="right" />
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'ISV-ground',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    }
  },

  mounted() {
    this.initReferenceData()
    if (this.formData) this.form = this.formData
  },

  data: () => ({
    labelPosition: 'left',
    formName: 'ISVGroundForm',
    // Dataset
    dataset: {},

    form: {
      type: 'ground',
      description: 'BĐS đất nền',
      valuation_value: null,
      guarantee_value: null,
      project_name: null,
      ground_type: null,
      land_area: null,
      GFA: null
    },

    rules: {
      valuation_value: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      guarantee_value: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      project_name: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      ground_type: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      land_area: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      GFA: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }]
    }
  }),

  methods: {
    initReferenceData() {
      this.$store.dispatch(
        'iDecision/retrieveReferenceData', 
        { dataset: 'ground' },
        { root: true }
      ).then(dataset => this.dataset = dataset)
    },

    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch(
            'iDecision/addCollateral', 
            { payload: this.form, path: 'ground', keep: keep }, 
            { root: true } 
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style lang="css" scoped>



