<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="ResidentialLandForm"
    label-width="220px" 
    size="mini">
    <el-form-item label="Giá trị định giá" prop="valuation_value">
      <el-input 
        v-model.number="form.valuation_value"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>
    
    <el-form-item label="NV đang đảm bảo" prop="guarantee_value">
      <el-input 
        v-model.number="form.guarantee_value"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Địa chỉ" prop="address">
      <el-select
        v-if="!readOnly"
        v-model="form.address"
        filterable
        remote
        reserve-keyword
        placeholder="Please enter a keyword"
        :remote-method="getAddress"
        :loading="loading"
        @change="getRoad">
        <el-option
          v-for="item in dataset.address"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.address" />
    </el-form-item>

    <el-form-item v-if="dataset.road || form.road" label="Đoạn đường" prop="road">
      <el-select v-if="dataset.road" v-model="form.road">
        <el-option
          v-for="item in dataset.road"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.road" />
    </el-form-item>

    <el-form-item v-if="dataset.height || form.height" label="Chiều cao" prop="height">
      <el-select v-if="dataset.height" v-model="form.height">
        <el-option
          v-for="item in dataset.height"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.height" />
    </el-form-item>

    <el-form-item v-if="dataset.location || form.location" label="Vị trí" prop="location">
      <el-select v-if="dataset.location" v-model="form.location">
        <el-option
          v-for="item in dataset.location"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.location" />
    </el-form-item>

    <el-form-item v-if="dataset.basement || form.basement" label="Số tầng ngầm" prop="basement">
      <el-select v-if="dataset.basement" v-model="form.basement">
        <el-option
          v-for="item in dataset.basement"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.basement" />
    </el-form-item>

    <el-form-item v-if="dataset.real_estate_type || form.real_estate_type" label="Loại bất động sản" prop="real_estate_type">
      <el-select v-if="dataset.real_estate_type" v-model="form.real_estate_type" @change="getStorey">
        <el-option
          v-for="item in dataset.real_estate_type"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.real_estate_type" />
    </el-form-item>

    <el-form-item v-if="dataset.storey || form.storey" label="Số tầng cao" prop="storey">
      <el-select v-if="dataset.storey" v-model="form.storey" @change="getData">
        <el-option
          v-for="item in dataset.storey"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.storey" />
    </el-form-item>

    <el-form-item v-if="dataset.roof || form.roof" label="Mái nhà" prop="roof">
      <el-select v-if="dataset.roof" v-model="form.roof" @change="getData">
        <el-option
          v-for="item in dataset.roof"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.roof" />
    </el-form-item>

    <el-form-item v-if="dataset.pillar || form.pillar" label="Số trụ cột" prop="pillar">
      <el-select v-if="dataset.pillar" v-model="form.pillar" @change="getData">
        <el-option
          v-for="item in dataset.pillar"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.pillar" />
    </el-form-item>

    <el-form-item v-if="dataset.base || form.base" label="Nền sàn" prop="base">
      <el-select v-if="dataset.base" v-model="form.base" @change="getData">
        <el-option
          v-for="item in dataset.base"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.base" />
    </el-form-item>

    <el-form-item v-if="dataset.wall || form.wall" label="Tường nhà" prop="wall">
      <el-select v-if="dataset.wall" v-model="form.wall" @change="getData">
        <el-option
          v-for="item in dataset.wall"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.wall" />
    </el-form-item>

    <el-form-item v-if="dataset.floor || form.floor" label="Sàn nhà" prop="floor">
      <el-select v-if="dataset.floor" v-model="form.floor" @change="getData">
        <el-option
          v-for="item in dataset.floor"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.floor" />
    </el-form-item>

    <el-form-item v-if="dataset.outbuilding || form.outbuilding" label="Khu phụ" prop="outbuilding">
      <el-select v-if="dataset.outbuilding" v-model="form.outbuilding" @change="getData">
        <el-option
          v-for="item in dataset.outbuilding"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <el-input v-else v-model="form.outbuilding" />
    </el-form-item>

    <el-form-item prop="land_area">
      <label slot="label">Diện tích đất
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            Đơn vị m2
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-input-number 
        v-model.number="form.land_area" 
        :min="0" 
        controls-position="right" />
    </el-form-item>

    <el-form-item prop="GFA">
      <label slot="label">Tổng diện tích sàn xây dựng
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            Đơn vị m2
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-input-number 
        v-model.number="form.GFA" 
        :min="0" 
        controls-position="right" />
    </el-form-item>

    <el-form-item label="Năm xây dựng" prop="year_build">
      <el-input-number 
        v-model.number="form.year_build" 
        :max="new Date().getFullYear()" 
        :min="1900"
        controls-position="right" />
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'ISV-residential-land',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    }
  },

  mounted() {
    if (this.formData) this.form = this.formData
  },
  
  watch: {
    'form.address'() {
      if (!this.readOnly) {
        this.form.road = null
        this.form.real_estate_type = null
        this.form.storey = null
        this.dataset.pillar = null
        this.dataset.roof = null
        this.dataset.base = null
        this.dataset.outbuilding = null
        this.dataset.wall = null
        this.dataset.floor = null
      }
    },

    'form.real_estate_type'() {
      if (!this.readOnly) {
        this.form.storey = null
        this.dataset.storey = null
      }
    },

    'form.storey'() {
      if (!this.readOnly) {
        this.form.pillar = null
        this.form.roof = null
        this.form.base = null
        this.form.outbuilding = null
        this.form.wall = null
        this.form.floor = null
      }
    }

  },

  data: () => ({
    loading: false,
    labelPosition: 'left',
    formName: 'ResidentialLandForm',
    // Dataset
    dataset: {
      address: null,
      height: null,
      location: null,
      road: null,
      real_estate_type: null,
      storey: null,
      pillar: null,
      roof: null,
      base: null,
      outbuilding: null,
      wall: null,
      floor: null
    },
    form: {
      type: 'house',
      description: 'BĐS thổ cư',
      valuation_value: null,
      guarantee_value: null,
      address: null,
      height: null,
      location: null,
      basement: null,
      road: null,
      real_estate_type: null,
      storey: null,
      pillar: null,
      roof: null,
      base: null,
      outbuilding: null,
      wall: null,
      floor: null,
      land_area: null,
      GFA: null,
      year_build: null
    },

    rules: {
      valuation_value  : [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      guarantee_value  : [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      project_name     : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      valuation_value  : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      address          : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      height           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      location         : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      basement         : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      road             : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      real_estate_type : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      storey           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      pillar           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      roof             : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      base             : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      outbuilding      : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      wall             : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      floor            : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      GFA              : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      year_build       : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      land_area        : [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }]
    }
  }),

  methods: {
    searchAddress(keyword) {
      this.$store.dispatch(
        'iDecision/retrieveReferenceData', 
        { dataset: 'house', keyword: keyword },
        { root: true }
      )
      .then(res => this.dataset.address = res.address)
      .finally(_ => this.loading = false)
    },

    getAddress(query) {
      if (query !== '') {
        this.loading = true
        this.searchAddress(query)
      } else {
        this.dataset.address = []
      }
    },

    getRoad() {
      this.$store.dispatch(
        'iDecision/retrieveReferenceData', 
        { dataset: 'house', ...this.form },
        { root: true }
      ).then(res => {
        this.dataset = Object.assign(this.dataset, res)
      })
    },

    getStorey() {
      setTimeout(() => {
        this.$store.dispatch(
          'iDecision/retrieveReferenceData', 
          { dataset: 'house', ...this.form },
          { root: true }
        ).then(res => this.dataset.storey = res.storey)
      }, 500)
    },

    getData() {
      setTimeout(() => {
        this.$store.dispatch(
          'iDecision/retrieveReferenceData', 
          { dataset: 'house', ...this.form },
          { root: true }
        ).then(res => {
          this.dataset = Object.assign(this.dataset, res)
        })
      }, 500)
    },

    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch(
            'iDecision/addCollateral', 
            { payload: this.form, path: 'house', keep: keep }, 
            { root: true } 
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>



