<template>
  <el-dialog 
    v-if="component"
    :visible="dialogNewCollateralVisible"
    :close-on-click-modal="false"
    :before-close="TOGGLE_NEW_COLLATERAL_DIALOG"
    :width="'40%'"
    :top="'3.5vh'">
    <span slot="title" class="subheading">
      <i :class="`el-icon-${component.icon}`" /> {{ component.label }}
    </span> 

    <component v-if="dialogNewCollateralVisible" :is="component.component" :readOnly="readOnly" :formData="formData" ref="form" />

    <span v-if="!readOnly" slot="footer" class="dialog-footer">
      <el-button size="small" type="primary" @click="submit(false)">Save</el-button>
      <el-button size="small" type="primary" @click="submit(true)">Save & Add another</el-button>
      <el-button size="small" @click="TOGGLE_NEW_COLLATERAL_DIALOG">Cancel</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import RSVAuto from './RSV/Auto'
import ISVGround from './ISV/Ground'
import ISVApartment from './ISV/Apartment'
import ISVResidentialLand from './ISV/ResidentialLand'
import TCBond from './Others/TCBond'
import Saving from './Others/Saving'

export default {
  props: {
    component: {
      type: Object,
      default: () => {}
    },

    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    }
  },

  components: {
    RSVAuto,
    ISVGround,
    ISVApartment,
    ISVResidentialLand,
    TCBond,
    Saving
  },

  computed: {
    ...mapGetters('iDecision', ['dialogNewCollateralVisible'])
  },

  methods: {
    ...mapMutations('iDecision', ['TOGGLE_NEW_COLLATERAL_DIALOG']),

    submit(keep) {
      this.$refs.form.submit(keep)
    }
  }
}
</script>
<style lang="css" scoped>
>>>.el-form-item__label {
  font-weight: 500;
}
>>>.el-form-item--small.el-form-item {
  margin-bottom: 0;
}
>>>.el-select {
  width: 100%;
}
>>>.el-dialog__body {
  padding: 20px 30px;
}
/*>>>.el-dialog__footer {
  padding: 0 30px;
  padding-bottom: 20px;
}*/
>>>.el-input-number .el-input__inner {
  text-align: left;
}
</style>



