<template>
  <el-dialog 
    v-if="component"
    :visible="dialogNewIncomeVisible"
    :before-close="TOGGLE_NEW_INCOME_DIALOG"
    :width="'45%'"
    :top="'3.5vh'">

    <span slot="title" class="subheading">
      <i :class="`el-icon-${component.icon}`" /> {{ component.label }}
    </span> 

    <component 
      v-if="dialogNewIncomeVisible" 
      :is="component.component" 
      :type="component.id"
      :description="component.label" 
      :readOnly="readOnly" 
      :formData="formData" 
      ref="OCDForm" />

    <span v-if="!readOnly" slot="footer" class="dialog-footer">
      <el-button size="small" type="primary" @click="submit(false)">Save</el-button>
      <el-button size="small" type="primary" @click="submit(true)">Save & Add another</el-button>
      <el-button size="small" @click="TOGGLE_NEW_INCOME_DIALOG">Cancel</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import OCDSalary from './OCD/Salary'
import OCDAuto from './OCD/Auto'
import OCDHouseholdBusiness from './OCD/Household'
import OCDRealEstate from './OCD/RealEstate'
import Company from './Others/Company'
import ExpectedAssetExploitation from './Others/ExpectedAssetExploitation'
import Liquidation from './Others/Liquidation'
import OthersIncome from './Others/OthersIncome'

export default {
  props: {
    component: {
      type: Object,
      default: () => {}
    },

    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    }
  },

  components: {
    OCDSalary,
    OCDAuto,
    OCDHouseholdBusiness,
    OCDRealEstate,
    Company,
    ExpectedAssetExploitation,
    Liquidation,
    OthersIncome
  },

  computed: {
    ...mapGetters('iDecision', ['dialogNewIncomeVisible'])
  },

  methods: {
    ...mapMutations('iDecision', ['TOGGLE_NEW_INCOME_DIALOG']),

    submit(keep) {
      this.$refs.OCDForm.submit(keep)
    }
  }
}
</script>
<style lang="css" scoped>
>>>.el-select {
  width: 100%;
}
>>>.el-dialog__body {
  padding: 20px 30px;
}
</style>



