<template>
  <div>
    <!-- <el-row :gutter="10" type="flex" class="mb-3">
      <el-col :span="3" :offset="18" class="text-md-left">
        <span class="font-weight-medium mr-2"> LTV </span>
        <el-tag size="medium" class="font-weight-medium"> 123% </el-tag>
      </el-col>
      <el-col :span="3" class="text-md-center">
        <el-tag effect="dark" size="small" class="font-weight-bold">Chưa phù hợp</el-tag>
      </el-col>
    </el-row> -->
    <el-table
      :data="_case.collaterals"
      border
      size="mini"
      ref="collateralTable"
      style="width: 100%">
      <el-table-column type="index" width="35" align="center" fixed="left" />
      <el-table-column
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :fixed="c.fixed"
        :sortable="c.sortable">
        <template slot-scope="scope">
          <span v-if="!['description'].includes(c.prop)">
            {{ scope.row[c.prop] | numberWithDots }}
          </span>
          <span v-else>
            {{ scope.row[c.prop] }}
          </span>
        </template>
      </el-table-column>

      <el-table-column
        fixed="right"
        width="75">
        <template slot-scope="scope">
          <el-button 
            type="text" 
            class="info--text"
            icon="icon icon-search"
            @click.native.prevent="viewDetail(scope.row)">
          </el-button>
          <el-button
            type="text"
            class="danger--text"
            icon="icon icon-bin2"
            @click.native.prevent="removeCollateral(scope.$index)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dropdown 
      class="mt-2" 
      trigger="click"
      placement="bottom-start" 
      @command="selectCollateral">

      <el-button type="primary" size="mini" plain>
        <i class="icon icon-plus el-icon--left" /> Thêm tài sản
      </el-button>

      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item 
          v-for="c in collateral_opts"
          :key="c.component"
          :icon="`el-icon-${c.icon}`"
          :command="c">
          {{ c.label }}
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <NewCollateralDialog :component="component" :readOnly="readOnly" :formData="formData" />
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'
import NewCollateralDialog from './NewCollateralDialog'

export default {
  name: 'iDecision-collateral',

  components: {
    NewCollateralDialog
  },

  computed: {
    ...mapGetters('iDecision', ['_case'])
  },

  data: () => ({
    collateral_opts: [
      { id: 'ground', label: 'BĐS - Đất nền', icon: 's-home', component: 'ISVGround' },
      { id: 'house', label: 'BĐS - Đất thổ cư', icon: 's-home', component: 'ISVResidentialLand' },
      { id: 'apartment', label: 'BĐS - Căn hộ', icon: 'office-building', component: 'ISVApartment' },
      { id: 'auto', label: 'Ôtô', icon: 'truck', component: 'RSVAuto' },
      { id: 'tcbond', label: 'TCBond', icon: 'document', component: 'TCBond' },
      { id: 'saving', label: 'Sổ tiết kiệm', icon: 's-finance', component: 'Saving' }
    ],

    component: null,
    readOnly: false,
    formData: null,

    columnDefs: [
      { label: 'Loại tài sản đảm bảo', prop: 'description', width: '200', sortable: true, fixed: 'left' },
      { label: 'Giá trị định giá', prop: 'valuation_value', sortable: true },
      { label: 'Nghĩa vụ đang được đảm bảo', prop: 'guarantee_value', sortable: true },
      { label: 'Giá trị thực', prop: 'real_value', sortable: true }
    ]
  }),

  methods: {
    ...mapMutations('iDecision', ['TOGGLE_NEW_COLLATERAL_DIALOG']),
    ...mapActions('iDecision', ['removeCollateral']),

    selectCollateral(component) {
      this.formData = null
      this.readOnly = false
      this.component = component
      this.TOGGLE_NEW_COLLATERAL_DIALOG()
    },

    viewDetail(collateral) {
      this.formData = collateral
      this.readOnly = true
      this.component = this.collateral_opts.find(i => i.id === collateral.type)
      this.TOGGLE_NEW_COLLATERAL_DIALOG()
    }
  }
}
</script>



