<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="LiquidationIncomeForm"
    label-width="170px" 
    size="mini">
    <el-form-item label="Đối tượng chứng minh" prop="subject">
      <el-select v-model="form.subject">
        <el-option 
          v-for="(o, i) in subjects" 
          :key="i"
          :label="o.label" 
          :value="o.value" />
      </el-select>
    </el-form-item>

    <el-form-item label="Loại tài sản" prop="asset_type">
      <el-radio-group v-model="form.asset_type">
        <el-radio-button label="real_estate">BĐS</el-radio-button>
        <el-radio-button label="movable">Động sản</el-radio-button>
        <el-radio-button label="passbook">Sổ tiết kiệm</el-radio-button>
        <el-radio-button label="tcbond">TCBond</el-radio-button>
      </el-radio-group>
    </el-form-item>

    <el-form-item label="Loại hình" prop="category">
      <el-select v-model="form.category">
        <el-option 
          v-for="(o, i) in categories" 
          :key="i"
          :label="o.label" 
          :value="o._id" />
      </el-select>
    </el-form-item>

    <el-form-item label="Giá trị tài sản" prop="asset_value">
      <el-input 
        v-model.number="form.asset_value"
        clearable>
        <template slot="append">đồng</template>
      </el-input>
    </el-form-item>
  </el-form>
</template>

<script>
import { subjects } from '@/api/otherFI'

export default {
  name: 'income-liquidation',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    },

    type: {
      type: String,
      default: () => 'Unknown'
    },

    description: {
      type: String,
      default: () => 'Unknown'
    }
  },

  mounted() {
    this.initReferenceData()
    if (this.formData) this.form = this.formData
  },

  computed: {
    categories() {
      return this.dataset[this.form.asset_type]
    }
  },

  watch: {
    'form.asset_type'() {
      this.form.category = null
    }
  },
  
  data() {
    const isNumber = (rule, value, callback) => {
      if (!value) return callback(new Error('Vui lòng nhập'))
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Vui lòng nhập số'))
        }
        callback()
      }, 50)
    }
    const type = this.type
    const description = this.description
    return {
      labelPosition: 'left',
      // Dataset
      subjects,
      dataset: {},
      formName: 'LiquidationIncomeForm',
      form: {
        type,
        description,
        subject     : null,
        asset_type  : null,
        category    : null,
        asset_value : null
      },

      rules: {
        subject     : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        asset_type  : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        category    : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
        asset_value : [{ validator: isNumber, required: true, trigger: 'blur' }]
      }
    }
  },

  methods: {
    initReferenceData() {
      this.$store.dispatch(
        'iDecision/retrieveReferenceData', 
        { dataset: 'liquidate' },
        { root: true }
      ).then(dataset => this.dataset = dataset)
    },

    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch(
            'iDecision/addIncome', 
            { payload: this.form, path: 'liquidate', keep: keep }, 
            { root: true } 
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>



